import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from "./ui/dropdown-menu";
import { Button } from "./ui/button";
import { getUser } from "../api/auth";
import { getToken, logout as clearToken } from "../utils/auth";

function Header() {
  const [username, setUsername] = useState("");
  const [isDarkMode, setIsDarkMode] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const token = getToken(); //    localStorage
        if (!token) {
          navigate("/login"); //    ,   
          return;
        }
        const response = await getUser(token);
        setUsername(response.data.username); //   
      } catch (error) {
        console.error("   :", error);
        clearToken();
        navigate("/login"); //       
      }
    };

    fetchUserData();
  }, [navigate]);

  const handleLogout = () => {
    clearToken(); //    localStorage
    navigate("/login"); //    
  };

  const handleThemeChange = () => {
    setIsDarkMode((prevMode) => !prevMode);
    document.documentElement.classList.toggle("dark", !isDarkMode); //  
  };

  const handleProfileSettings = () => {
    navigate("/profile/edit"); //     
  };

  return (
    <header className="bg-gray-800 text-white py-4 px-6 shadow-md">
      <div className="container mx-auto flex justify-between items-center">
        {/*  */}
        <Link to="/" className="text-2xl font-bold hover:text-gray-300">
          Delivery System
        </Link>

        {/*  */}
        <nav className="flex space-x-4">
          <Link to="/routes" className="hover:underline hover:text-gray-300">
            
          </Link>
        </nav>

        {/*    */}
        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <Button variant="ghost" className="text-white hover:bg-gray-700">
              {username || "..."}
            </Button>
          </DropdownMenuTrigger>
          <DropdownMenuContent className="bg-white text-black shadow-lg rounded-lg mt-2">
            <DropdownMenuItem onClick={handleProfileSettings}>
               
            </DropdownMenuItem>
            <DropdownMenuItem onClick={handleThemeChange}>
              {isDarkMode ? " " : " "}
            </DropdownMenuItem>
            <DropdownMenuItem onClick={handleLogout}>
              
            </DropdownMenuItem>
          </DropdownMenuContent>
        </DropdownMenu>
      </div>
    </header>
  );
}

export default Header;
